import React, { Component } from 'react';
import { Button } from 'reactstrap';
import "./Home.css"
import Footer from "../Footer/Footer"

class Home extends Component {
    render(){
        return (
            <div>
            <div className="Home">
                <div className="container">
                    <div className="content">
                        <h1>Naruto Uzumaki</h1>
                        <h2>Shinobi</h2>
                        <p>Shinobi Maher stopped the Great Ninja War, sealed Kajoya,<br /> became a hawkage, and persuaded his friend Sasaki to return to the village.</p>
                        <Button color="secondary">Let's Begin</Button>
                    </div>
                </div>
            </div>
            <Footer />
            </div>
            );
        }
}

export default Home;
