import React, { Component } from 'react';
import { Button, Form, FormGroup , Input   } from 'reactstrap';
import "./Contact.css";
import Footer from "../Footer/Footer"

class Contact extends Component {
    render(){
        return (
            <div>
                 <div className="contact text-center">
                     <div className="container">
                     <h1>Contact Me</h1>
                     <p className="lead">Feel Free To Contact with Us Any Time</p>
                     <div className="row">
                            <div className="col-md-6 col-xs-12">
                                <Form>
                                    <FormGroup>
                                        <Input type="text" name="text" id="exampleEmail" placeholder="Your Name" />
                                    </FormGroup>
                                    <FormGroup>
                                        <Input type="email" name="email" id="examplePassword" placeholder="Your Email" />
                                        </FormGroup>
                                </Form>
                            </div>
                            <div className="col-md-6 col-xs-12">
                                <Form>
                                    <FormGroup>
                                    <Input type="textarea" name="text" id="exampleText" placeholder="Your Messge" />
                                    </FormGroup>
                                </Form>
                            </div>
                     </div>
                     </div>
                     <Button color="secondary">Send</Button>
                 </div>
                 <Footer />
            </div>
            );
        }
}

export default Contact;
